import './Face.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { memo } from 'react';

interface Props {
  icon?: JSX.Element;
  onClick?: () => void;
}

const Face = ({
  icon = <FontAwesomeIcon icon={['far', 'meh']} />,
  onClick,
}: Props) => (
  <button type="button" className="Face" onClick={onClick}>
    {icon}
  </button>
);

export default memo(Face);
