import './MineFieldParams.scss';

import Radio from 'components/Radio';
import { mineFieldsParams } from 'constants/mine-fields-params';
import { MinesweeperContext } from 'contexts/minesweeper';
import { useCallbackRef } from 'hooks/use-callback-ref';
import React, { useContext } from 'react';

const MineFieldParams = () => {
  const { mineFieldParams, setMineFieldParams } = useContext(
    MinesweeperContext
  );

  const className = 'MineFieldProps';
  const itemClassName = `${className}Item`;

  const handleMineFieldPropsChange = useCallbackRef(
    (props: MineFieldParams) => {
      setMineFieldParams(props);
    }
  );

  return (
    <form className={className}>
      {mineFieldsParams.map((item) => {
        const { name, id } = item;

        return (
          <Radio
            item={item}
            name="MineFieldProps"
            className={itemClassName}
            checked={name === mineFieldParams.name}
            onChange={handleMineFieldPropsChange}
            key={id}
          />
        );
      })}
    </form>
  );
};

export default MineFieldParams;
