import './Minesweeper.scss';

import Footer from 'components/Footer';
import Header from 'components/Header';
import { MinesweeperContext } from 'contexts/minesweeper';
import { useMineField } from 'hooks/use-mine-field';
import React from 'react';

import Indicators from '../Indicators';
import MineField from '../MineField';
import MineFieldParams from '../MineFieldParams';

const Minesweeper = () => (
  <MinesweeperContext.Provider value={{ ...useMineField() }}>
    <Header />

    <main className="Minesweeper">
      <Indicators />
      <MineField />
      <MineFieldParams />
    </main>

    <Footer />
  </MinesweeperContext.Provider>
);

export default Minesweeper;
