import './Cell.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { memo, SyntheticEvent } from 'react';
import { CellChecker } from 'utils/cell-checker';
import { joinClassNames } from 'utils/join-class-names';

import Bomb from '../Bomb';

interface Props {
  cell: Cell;
  onClick?: (cell: Cell) => void;
  onMouseDown?: (event: SyntheticEvent<HTMLButtonElement>, cell: Cell) => void;
  onContextMenu?: (cell: Cell) => void;
}

const Cell = ({ cell, onClick, onMouseDown, onContextMenu }: Props) => {
  const {
    isHidden,
    isEmpty,
    isFlagged,
    isMined,
    isIncorrect,
    isBusted,
  } = new CellChecker(cell);

  const { value } = cell;

  const className = 'Cell';
  const visibleClassName = `${className}__visible`;

  const handleClick = () => {
    onClick?.(cell);
  };

  const handleContextMenu = () => {
    onContextMenu?.(cell);
  };

  const handleMouseDown = (event: SyntheticEvent<HTMLButtonElement>) => {
    onMouseDown?.(event, cell);
  };

  if (isHidden) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={className}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={className}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (isEmpty) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={joinClassNames(className, visibleClassName)}
      />
    );
  }

  if (isMined) {
    return <Bomb className={className} />;
  }

  if (isIncorrect) {
    return (
      <Bomb className={joinClassNames(className, `${className}__wrong`)} />
    );
  }

  if (isBusted) {
    return (
      <Bomb className={joinClassNames(className, `${className}__busted`)} />
    );
  }

  return (
    <button
      type="button"
      className={joinClassNames(
        className,
        `${className}__${value}`,
        visibleClassName
      )}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(Cell);
