import { CellAppearance } from 'constants/cell-appearance';
import { CellValue } from 'constants/cell-value';

export class CellChecker {
  constructor(private cell: Cell) {}

  private isEqual(key: keyof Cell, value: Cell[keyof Cell]) {
    return this.cell[key] === value;
  }

  get isEmpty() {
    return this.isEqual('value', CellValue.Empty);
  }

  get isMined() {
    return this.isEqual('value', CellValue.Mine);
  }

  get isIncorrect() {
    return this.isEqual('value', CellValue.Incorrect);
  }

  get isBusted() {
    return this.isEqual('value', CellValue.Busted);
  }

  get isHidden() {
    return this.isEqual('appearance', CellAppearance.Hidden);
  }

  get isFlagged() {
    return this.isEqual('appearance', CellAppearance.Flagged);
  }

  get isUnsolved() {
    return this.isMined && !this.isFlagged;
  }

  get isSolvedWrong() {
    return !this.isMined && this.isFlagged;
  }
}
