import { useMineField } from 'hooks/use-mine-field';
import { createContext } from 'react';

type Value = ReturnType<typeof useMineField>;

export const MinesweeperContext = createContext<Value>({
  mineField: [],
  mineFieldParams: {} as MineFieldParams,
  mineFieldStatus: {} as MineFieldStatus,
  /* eslint-disable @typescript-eslint/no-empty-function */
  setMineFieldParams: () => {},
  setupMineField: () => {},
  revealCell: () => {},
  toggleFlag: () => {},
  revealNeighbors: () => {},
  reset: () => {},
});
